<template>
  <div class="user" v-if="user">
    <div class="user__left">
      <img :src="user.avatar_url" alt="" />
      <a :href="user.html_url" target="_blank" class="user__btn">ПОСЕТИТЬ</a>
    </div>
    <div class="user__rigth">
      <p>{{ user.login }}</p>
      <p>
        Репозиториев: <span>{{ user.public_repos }}</span>
      </p>
      <p>
        Создан:
        <span>{{ new Date(user.created_at).toLocaleDateString() }}</span>
      </p>
      <p>
        Подписчиков: <span>{{ user.followers }}</span>
      </p>
      <p>
        Подписок: <span>{{ user.following }}</span>
      </p>
    </div>
  </div>
  <div class="sort">
    <h2 class="sort_title">Сортировка</h2>
    <div class="sort_box">
      <button
        class="sort_box-btn"
        v-for="(btn, idx) in btns"
        :key="idx"
        :class="{ active: isActive == idx }"
        @click="sortRepos(idx, btn.sortType)"
      >
        {{ btn.name }}
      </button>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["user"]),
  },
  data() {
    return {
      btns: [
        { name: "ИМЯ", sortType: "name" },
        { name: "ЗВЕЗДЫ", sortType: "stargazers_count" },
        { name: "ДАТА", sortType: "created_at" },
      ],
      isActive: 0,
    };
  },
  methods: {
    ...mapMutations(["sort"]),
    sortRepos(index, sortType) {
      this.isActive = index;
      this.sort(sortType);
    },
  },
};
</script>

